* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 520px;
  margin-bottom: 25px;
  font-size: 25px;
}
.title{
  font-size: 50px;
  font-weight:bold;
}

.country-info{
  font-size: 25px;
  margin-left: 30px;
}
span{
  font-weight: bold;
  color:green;
}

.country-list{
  margin-left:30px;
  font-size: 30px;
}

.wrapper{
  display:flex;
  align-items: baseline;
  
}
